.footer {
    background-color: #3bc0b2;
    padding: 3rem 0;
    h4 {
        font-weight: bold;
    }
    &__links {
        list-style: none;
    }

    a:link,
    a:visited {
        color: #fff;
        font-size: 1.6rem;
    }
}

.copyright {
    background-color: #2da89b;
    font-size: 1.6rem;

    a:link,
    a:visited {
        color: #1a5d79;
    }
}