.aditinav {
    background-color: var(--nav-bg);
    font-size: 1.6rem;

    a:link,
    a:visited {
        color: #fff!important;
    }


    .navbar-nav {
        flex: 1;
        display: flex;
        justify-content: space-between;

        @media only screen and (min-width: 75em) {
            margin-left: -20px!important;
            margin-right: -39px!important;
        }
        

    }


    .nav-item {
        display: flex;
        align-items: center;
        position: relative;

        @media only screen and (min-width: 75em) {
                 
            &:not(:first-child):before  {
                content: '';
                width: 3px;
                height: 2rem;
                background: #fff;
                display: inline-block;
                position: absolute;
                left: -2rem;
                right: 0;

               
            }  

        }
        
    }
   



    .navbar-toggler {
        margin-left: auto;
    }
}

