.featured__products {
    &-heading {
        padding: 2rem 0;
        color:#fff;
        background-color: #3bc0b2;
    }

    aside {
        padding: 4rem 0;

       
    }

    &-item {
        border: 1px solid #e4e4e4;
        border-bottom:.5rem solid #3cc395;
        
        padding: 3rem;
        
        @media only screen and (max-width: 991px) {
            // width: 450px;
            margin: 0 auto;
            overflow: hidden;
        } 
        @media only screen and (max-width: 500px) {
            width: 100%;
        }   
    }
}