.shop-btn {
    background-color: var(--content-shop-btn);
    font-size: 1.6rem;
    padding: 0 1.5rem;
    border: none;
}

h2, h3, .price {
    color: #a1a1a1;
}


.slider-btn {
    background-color: transparent;
    font-weight: bold;
    color: #fff;
    border-radius:1.4rem;
    padding: .5rem 2rem;
    border:.2rem solid #fff;
}