.slider {
    background-color: #3fc8b1;
    padding: 8rem 0;
    overflow: hidden;
    &__content {
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;

        @media only screen and (max-width: 37.5em) {
            flex-wrap: wrap;
        }
    }

    &__image {
        max-width: 100%;
        display: block;

        @media only screen and (max-width: 37.5em) {
            width: 70%;
            margin: 0 auto;
        }
    }

    &__text {
        color:#fff;
        h1 {
            font-size: 5rem;
            text-transform: uppercase;
        }

        p {
            font-size:  1.6rem;
        }

        button {
            font-size:  1.6rem;
        }

        @media only screen and (max-width: 37.5em) {
            text-align: center;
        }
    }
}