*, *::before, *::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

body {
    font-family: 'Candal', sans-serif;
}

.note {
    background-color: #232f32;
    padding: 2rem 0;

    &__container {
        max-width: 60%;
        margin: 0 auto;
        text-align: center;
        color: #fff;

        a {
            color: #2da89b;
        }
    }

    &__hide {
        margin: 0 1rem;
        padding: 0.1rem 2rem;
        background-color: #fff;
        border: 0;
        color: #333;
        cursor: pointer;
        transition: all .2s; 

        &:hover {
            background-color: #2da89b;
            color: #fff;
        }

    }

    &__p {
        display: inline-block;        
        font-size: 1.8rem;
    }

    &__link {
        
        transition: all .2s;
    }
    
}