.header {
    font-size: 1.6rem;
    padding: 2rem 0;

    &__row  {
        justify-content: space-between;
        align-items: center;

        @media only screen and (max-width: 575px) {
            flex-direction: column;
        }
    }
    &__logo{
        height: 4rem;
        @media only screen and (max-width: 575px) {
            margin-bottom: 2rem;
        }
    }

}

.search {
    flex: 0 0 40%;

    display: flex;
    justify-content: center;

    &__input {
        width: 90%;
        margin-right: -3.25rem;
    }


    &__button {
        border: 0;
        background-color: transparent;
        margin-top: -1rem;
    }

    &__icon {
        height: 2rem;
        width: 2rem;
        fill: var(--gray);
    }

}

.cart {
    align-items: center;
    justify-content: space-between;
    flex: 0 0 8%;

    &__header-icon {
        height: 3rem;
        width: 3rem;
        fill: var(--primary-color);
    }

    
}

