.product__category {
    padding: 4rem 0;

    

    &-content {
        align-items: center;
    }

    &-item {
        align-items: center;

        @media only screen and (min-width: 40em) {
            &:not(:last-child):before  {
                content: '';
                width: 1px;
                height: 26rem;
                background: #e7e7e7;
                display: inline-block;
                position: absolute;
                right: -12px;
                z-index: -1;
            }
        }

        @media only screen and (max-width: 991px) {
            &::before {
                content: ''!important;
                width: 0!important;
            }
        }
    }

    

}